<template><undefined>
<h1>Button 按钮</h1>
<p>按钮用于开始一个即时操作。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>标记了一个（或封装一组）操作命令，响应用户点击行为，触发相应的业务逻辑。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-button type=\&quot;primary\&quot;&gt;Primary&lt;/v-button&gt;\n&lt;v-button&gt;Default&lt;/v-button&gt;\n&lt;v-button type=\&quot;dashed\&quot;&gt;Dashed&lt;/v-button&gt;\n&lt;v-button type=\&quot;danger\&quot;&gt;Danger&lt;/v-button&gt;\n&lt;v-button type=\&quot;info\&quot;&gt;Info&lt;/v-button&gt;\n&lt;v-button type=\&quot;success\&quot;&gt;Success&lt;/v-button&gt;\n&lt;v-button type=\&quot;warning\&quot;&gt;Warning&lt;/v-button&gt;\n&lt;v-button type=\&quot;error\&quot;&gt;Error&lt;/v-button&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-button type="primary">Primary</v-button>
<v-button>Default</v-button>
<v-button type="dashed">Dashed</v-button>
<v-button type="danger">Danger</v-button>
<v-button type="info">Info</v-button>
<v-button type="success">Success</v-button>
<v-button type="warning">Warning</v-button>
<v-button type="error">Error</v-button>
</div>
                  <div slot="description"><h4 id="an-niu-lei-xing"><a class="header-anchor" href="#an-niu-lei-xing" aria-hidden="true">¶</a> 按钮类型</h4>
<p>按钮有四种类型：主按钮、次按钮、虚线按钮、危险按钮以及四种颜色按钮。主按钮在同一个操作区域最多出现一次。</p>
</div>
