                  <div slot="description"><h4 id="an-niu-chi-cun"><a class="header-anchor" href="#an-niu-chi-cun" aria-hidden="true">¶</a> 按钮尺寸</h4>
<p>按钮有大、中、小三种尺寸。通过设置 size 为 large small 分别把按钮设为大、小尺寸。若不设置 size，则尺寸为中。</p>
</div>
