<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div&gt;\n        &lt;v-button type=\&quot;primary\&quot; :loading=\&quot;loading\&quot;&gt;{{ loading ? \&quot;Loading\&quot; : \&quot;Default\&quot; }}&lt;/v-button&gt;\n        &lt;v-button type=\&quot;primary\&quot; @click=\&quot;removeLoading\&quot;&gt;点击将切换loading状态&lt;/v-button&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data: function () {\n            return {\n                loading: true\n            };\n        },\n        methods: {\n            onClick: function() {\n                alert(\&quot;Button Clicked！\&quot;)\n            },\n            removeLoading: function() {\n                this.loading = !this.loading;\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <div>
        <v-button type="primary" :loading="loading">{{ loading ? "Loading" : "Default" }}</v-button>
        <v-button type="primary" @click="removeLoading">点击将切换loading状态</v-button>
    </div>
</template>


</div>
                  <div slot="description"><h4 id="jia-zai-zhong-zhuang-tai"><a class="header-anchor" href="#jia-zai-zhong-zhuang-tai" aria-hidden="true">¶</a> 加载中状态</h4>
<p>添加 loading 属性即可让按钮处于加载状态，最后两个按钮演示点击后进入加载状态。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">:loading</span>=<span class="hljs-string">"loading"</span>&gt;</span><span>{{</span> loading ? "Loading" : "Default" <span>}}</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"removeLoading"</span>&gt;</span>点击将切换loading状态<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-keyword">function</span> (<span class="hljs-params"></span>) </span>{
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">loading</span>: <span class="hljs-literal">true</span>
            };
        },
        <span class="hljs-attr">methods</span>: {
            <span class="hljs-attr">onClick</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
                alert(<span class="hljs-string">"Button Clicked！"</span>)
            },
            <span class="hljs-attr">removeLoading</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
                <span class="hljs-keyword">this</span>.loading = !<span class="hljs-keyword">this</span>.loading;
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
