<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="button-props"><a class="header-anchor" href="#button-props" aria-hidden="true">¶</a> Button Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>设置按钮类型</td>
<td>string</td>
<td>primary dashed info success warning danger error 或者不设</td>
<td>-</td>
</tr>
<tr>
<td>html-type</td>
<td>设置 button 原生的 type 值</td>
<td>string</td>
<td>参考 HTML 标准（button reset submit）</td>
<td>button</td>
</tr>
<tr>
<td>icon</td>
<td>设置按钮的图标类型</td>
<td>string</td>
<td>参考Icon组件中的type可选值</td>
<td>-</td>
</tr>
<tr>
<td>shape</td>
<td>设置按钮形状</td>
<td>string</td>
<td>circle circle-outline 或者不设</td>
<td>-</td>
</tr>
<tr>
<td>size</td>
<td>设置按钮大小</td>
<td>string</td>
<td>small large 或者不设</td>
<td>default</td>
</tr>
<tr>
<td>loading</td>
<td>设置按钮载入状态</td>
<td>boolean</td>
<td>true false</td>
<td>false</td>
</tr>
<tr>
<td>ghost</td>
<td>幽灵属性，使按钮背景透明</td>
<td>boolean</td>
<td>true false</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="button-group-props"><a class="header-anchor" href="#button-group-props" aria-hidden="true">¶</a> Button Group Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td>设置按钮大小</td>
<td>string</td>
<td>small large 或者不设</td>
<td>default</td>
</tr>
</tbody>
</table>
<h3 id="button-events"><a class="header-anchor" href="#button-events" aria-hidden="true">¶</a> Button Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>click</td>
<td>点击按钮时触发的事件</td>
<td>事件对象</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
    export default {
        data: function () {
            return {
                loading: true
            };
        },
        methods: {
            onClick: function() {
                alert("Button Clicked！")
            },
            removeLoading: function() {
                this.loading = !this.loading;
            }
        }
    }
</script>


